<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process204"></a>

<p><br></p>
<p><span class="rvts17">无路可逃（</span><span class="rvts50">No Way Out</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:653,Y:146</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：欧文</span><span class="rvts20"></span><span class="rvts21">安可瑞特（</span><span class="rvts20">Oven Anchoret</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">营救瑞文或帮助魔导师杀掉瑞文</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1258.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在黑坑洞口处，一些魔导师试图烧掉瑞文的房子，逼出瑞文。如果此时我们身上有之前浮木城魔导师雷蒙的通行证，可以说服这些魔导师并进入着火的屋子里面的地下室与瑞文谈话。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1259.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">与欧文谈话，他会告诉你事由一把剑引起。尝试说服欧文交出剑，或帮助欧文打败屋外的魔导师。一旦选择与魔导师发生战斗，所有黑坑区域的魔导师都将变为敌对状态。</span>
<br><span class="rvts21">并且，欧文是一个无护甲的角色，注意保护他。保护欧文，打败魔导师，并使欧文存活下来，是该任务取得最大经验值的方法。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



